import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { PedidoService } from '../pedido.service';
import { Pedido } from '../pedido';

@Component({
  selector: 'app-pedido-sucesso',
  templateUrl: './pedido-sucesso.page.html',
  styleUrls: ['./pedido-sucesso.page.scss'],
})
export class PedidoSucessoPage implements OnInit {

  constructor(private route: ActivatedRoute, private router: Router, private pedidoService: PedidoService) { }

  key: string;
  pedido: Pedido = new Pedido();

  ngOnInit() { 
    this.key = this.route.snapshot.paramMap.get('id');
    if (this.key){
      this.pedidoService.getChave(this.key).then((val)=>{ 
        this.pedido = val;
      })
    } 
  }  
  
  home(){
    this.router.navigate(['/home']);
  }
  novoPedido(){
    this.router.navigate(['/novo-pedido']);
  }
  editarPedido(){
    this.router.navigate(['/novo-pedido/' + this.key]);
  } 

}
